@import "ic-event-cards";
@import "ic-single";
@import "ic-tag";
@import 'ic-category';

// Blocks
@import "blocks/post-group";
@import "blocks/post-column";
@import "blocks/calendar-grid";


.pagination span.page-link {
    padding: .5rem 0.75rem;
}


/*--------------------------------------------------------------
3. Align left, align right, align center.
--------------------------------------------------------------*/
@include media-breakpoint-up($lg) {
    figure.wp-block-image.alignleft {
        float: left;
        margin-inline-end: 2rem;
        margin-inline-start: calc((100% - 776px) / 2) !important;
    }

    figure.wp-block-image.alignright {
        float: right;
        margin-inline-end: calc((100% - 776px) / 2) !important;
        margin-inline-start: 2rem;
    }

    figure.wp-block-image.aligncenter {
        max-width: fit-content;
        margin-inline-end: auto !important;
        margin-inline-start: auto !important;
    }

    // The following two rules target the block editor.
    // Both .wp-block and the .wp-block-image are present under .editor-styles-wrapper
    figure.wp-block.wp-block-image.alignleft {
        margin-inline-start: calc((100% - 1200px) / 2) !important;
    }

    figure.wp-block.wp-block-image.alignright {
        margin-inline-end: calc((100% - 1200px) / 2) !important;
    }
}