/*--------------------------------------------------------------
# Inner Circle Event Cards

1. Mobile
2. Desktop
--------------------------------------------------------------*/

#events .card-ic-event,
#event-breakout .card-ic-event {
    margin-bottom: $uds-size-spacing-4;

    .card-header {
        padding: $uds-size-spacing-2 $uds-size-spacing-3 0 $uds-size-spacing-3;
    }

    .card-details {
        padding: 0 $uds-size-spacing-3 $uds-size-spacing-2 $uds-size-spacing-3;

        svg {
            margin-right: $uds-size-spacing-1;
        }
    }

    .card-footer {
        a {
            font-size: 14px;
            color: $uds-color-base-gray-5;
            margin-right: $uds-size-spacing-2;
        }
    }
}

@include media-breakpoint-up($lg) {
    #events {
        padding: 0 0 0 $uds-size-spacing-4;
    }
}

#event-breakout {
    background-color: $uds-color-base-gray-2;
    padding: 6rem 0;
    max-width: 1920px;

    h2 {
        margin-left: 6px;
        margin-bottom: 2rem;
    }

    .event-card-container {
        display: flex;
        flex-direction: column;
        gap: 2rem;
        margin-bottom: 0;
    }
}

@include media-breakpoint-up($md) {
    #event-breakout .event-card-container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 1fr;
    }
}

@include media-breakpoint-up($lg) {
    #event-breakout .event-card-container {
        grid-template-columns: repeat(3, 1fr);
    }
}